.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
} 

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*
Tutorial
*/

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

.App > nav {
  border-bottom: 1px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  display:block;
  z-index: 2;
  opacity: 0.75;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
  float: left;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 20px 32px 17px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  /* max-width: 700px; */
  margin: auto;
  padding-top: 64px;
  width: 68%;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.article_main_image_element{
  max-width: 90%;
  height: auto;
}

.main_image_meta{
  color: #666;
  font-size: 14px;
  padding-bottom: 15px;
  font-family: 'Open Sans',sans-serif;
  text-align: left;
}

.article_paragraph{
  font-family: 'Open Sans',sans-serif;
  line-height: 2em;
  color: #000000;
  text-align: left;
  /* font-size:1vw; */

  padding-bottom: 1em;
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.home_main_image{
  width: 100%;
  background-repeat: no-repeat;
  background-image: url(https://imagestravelblog.s3.us-east-2.amazonaws.com/beachimage.png);
  /* background-image: url(https://imagestravelblog.s3.us-east-2.amazonaws.com/clearwater.png); */
  /* background-image: url(https://imagestravelblog.s3.us-east-2.amazonaws.com/IMG-9582.jpg); */
  background-attachment: scroll;
  background-position: center bottom;
  border: none;
  float: left;
  height: 500px;
  background-size: cover;
  position: static;
}

.home_image_container{
  /* padding-top: 58px; */
  position: relative;
}

.main_logo_conatiner {
  padding: 30px 20px 0 20px;
  height: 94%;
  transition: opacity .6s ease-in;
  /* display: table; */
}

.main_logo {
  width: 100%;
  border: none;
  margin: auto;
  height: 89%;
  position: absolute;
}

.main_logo span{
  font-family: Charmonman;
  font-size: 50px;
  font-weight: bold;
  /* color: darksalmon; */
}

.App nav ul a {
  opacity: 1;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-weight: bolder;
  text-transform: uppercase;
}

.hand_written_header{
  font-family: Reenie Beanie;
  font-size: 50px;
}

.social_wrapper {
  width: auto;
  margin: 0;
  left: 30px;
  text-align: left;
  position: absolute;
}

.social_block {
  height: 100%;
  padding: 10px 4px 0 4px;
  float: right;
}
.social_block a {
  padding: 8px;
}

.pinterestLogo{
  height: 28px;
  width:28px;
}

/* Latest Post Main Page */
.left_latest_post{
  background-color:#2196F3;  
}

.middle_latest_post{
  background-color:#f1f1f1;
}

.right_latest_post{
  background-color:#4CAF50;
}

.latest_post_container {
  width: 100%;  
  position: relative;
  /* display: inline-block; */
}

.latest_post_container > div{
  /* display: block; */
  float:left;
  width:33%;
  padding-top:100%;
  padding: 20px 20px 20px 20px;
  /* height: 100%; */
}

.aspect_container{
  width: 100%;
  padding-top: 100%; /* 1:1 Aspect Ratio */
  position: relative;
}

.aspectBox{
  position: absolute;
  background-color: #333033;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  color: white;
  font-weight: bold;
}

* {
  box-sizing:border-box;
}

/* Logo */
img.dosoutddors_logo_home {
  width: 45%;
  opacity: 1;
}

.image_filter {
  opacity: 0.05;
  background-color: #a9a9a9;
  height: 500px;
}
.aspectBox img{
  width: 100%;

}
@media screen and (max-width:800px) {
  .latest_post_container div {
    width:100%; /* The width is 100%, when the viewport is 800px or smaller */
  }
}